<template>
  <b-container fluid>
    <b-row>
      <b-col cols="8">
        <b-row>
          <b-form-textarea
            class="sini-textarea"
            id="textarea"
            v-model="text"
            rows="10"
            max-row="20"
            no-resize
            placeholder="Type some thing ..."
            @keyup="update_origin"
          ></b-form-textarea>
        </b-row>
        <b-row>
          <b-col cols="6">
            <b-button class="form-control sini-btn" variant="warning" @click="clear">
              <b-icon icon="trash"></b-icon>Clear
            </b-button>
          </b-col>
          <b-col cols="6">
            <b-button class="form-control sini-btn" variant="warning" @click="reset_origin">
              <b-icon icon="arrow-clockwise"></b-icon>Reset last typed
            </b-button>
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="4">
        <b-tabs content-class="mt-3">
          <b-tab title="Nomal" active>
            <b-row>
              <b-col cols="6">
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_sentence"
                  >Sentence case</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_upper"
                  >UPPER CASE</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_camel"
                  >camelCase</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_snake"
                  >snake_case</b-button>
                </b-row>
              </b-col>
              <b-col cols="6">
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_capitalized"
                  >Capitalized Case</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_lower"
                  >lower case</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_pascal"
                  >PascalCase</b-button>
                </b-row>
                <b-row>
                  <b-button
                    class="form-control sini-btn"
                    variant="primary"
                    @click="convert_kebab"
                  >kebab-case</b-button>
                </b-row>
              </b-col>
            </b-row>
          </b-tab>
          <b-tab title="Python">
            <p>Comming soon ...</p>
          </b-tab>
          <b-tab title="C#">
            <p>Comming soon ...</p>
          </b-tab>
        </b-tabs>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import Vue from 'vue';

export default {
  name: "ConvertString",
  data() {
    return {
      regex_const: {
        sentence: new RegExp('^[A-Z][a-z ]*$'),
        capitalized: new RegExp('^([A-Z][a-z]* *)+$'),
        lower: new RegExp('^[a-z ]+$'),
        upper: new RegExp('^[A-Z ]+$'),
        camel: new RegExp('^[a-z][A-Za-z]*$'),
        pascal: new RegExp('^([A-Z][a-z]*)+$'),
        snake: new RegExp('^[a-z][a-z_]*$'),
        kebab: new RegExp('^[a-z][a-z-]*$'),
      },
      text: '',
      origin: ''
    }
  },
  methods: {
    clear: function(){
      this.text = ''
    },
    update_origin: function(){
      this.origin = this.text
    },
    reset_origin: function(){
      this.text = this.origin 
    },
    pre_convert: function(){
      this.text = this.text.trim()
      if (this.regex_const.upper.test(this.text)) {
        return
      }
      else if (this.regex_const.camel.test(this.text) || this.regex_const.pascal.test(this.text)) {
        var temp_arr = []
        var pnt = 0
        for (var index in this.text) {
          if (this.text.charAt(index) == this.text.charAt(index).toUpperCase()){
            temp_arr.push(this.text.substring(pnt, index))
            pnt = index
          }
        }
        temp_arr.push(this.text.substring(pnt))
        this.text = temp_arr.join(' ').trim()
      }
      else if (this.regex_const.snake.test(this.text)) {
        this.text = this.text.split('_').join(' ')
      }
      else if (this.regex_const.kebab.test(this.text)) {
        this.text = this.text.split('-').join(' ')
      }
    },
    copy_to_clipboard: function(){
      setTimeout(function(){ 
        var textArea = document.querySelector('#textarea')
        textArea.select()
        try {
          document.execCommand('copy');
          Vue.swal({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 1000,
            icon: 'success',
            title: 'Copied',
          });
        } catch (err) {
          Vue.swal({
            toast: true,
            position: 'top-end',
            showConfirmButton: false,
            timer: 1000,
            icon: 'danger',
            title: 'Failed to copy',
          });
        }
      }, 100);
    },
    convert_sentence: function(){
      this.pre_convert()
      this.text = this.text.charAt(0).toUpperCase() + this.text.substr(1).toLowerCase()
      this.copy_to_clipboard()
    },
    convert_capitalized: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
      }
      this.text = arr_text.join(' ')
      this.copy_to_clipboard()
    },
    convert_upper: function(){
      this.pre_convert()
      this.text = this.text.trim().toUpperCase()
      this.copy_to_clipboard()
    },
    convert_lower: function(){
      this.pre_convert()
      this.text = this.text.trim().toLowerCase()
      this.copy_to_clipboard()
    },
    convert_camel: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        if (index == 0){
          arr_text[index] = arr_text[index].toLowerCase()
        }
        else {
          arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
        }
      }
      this.text = arr_text.join('')
      this.copy_to_clipboard()
    },
    convert_pascal: function(){
      this.pre_convert()
      var arr_text = this.text.split(' ')
      for (var index in arr_text) {
        arr_text[index] = arr_text[index].charAt(0).toUpperCase() + arr_text[index].substr(1).toLowerCase()
      }
      this.text = arr_text.join('')
      this.copy_to_clipboard()
    },
    convert_snake: function(){
      this.pre_convert()
      this.text = this.text.toLowerCase().split(' ').join('_')
      this.copy_to_clipboard()
    },
    convert_kebab: function(){
      this.pre_convert()
      this.text = this.text.toLowerCase().split(' ').join('-')
      this.copy_to_clipboard()
    },
  }
};
</script>

<style scoped>
.sini-btn {
  margin: 10px;
  font-weight: bolder;
}
.sini-textarea {
  margin: 10px;
}
</style>
